import { Routes, RouterModule} from '@angular/router';

import { PagesComponent } from './pages.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { UserRegComponent } from '../user-reg/user-reg.component';
import { ManageUserComponent } from './manage-user/manage-user.component';
import { PreflangComponent } from './preflang/preflang.component';
import { MainComponent } from './my/main/main.component';
import { UruspenggunaComponent } from './my/uruspengguna/uruspengguna.component';
/*import { PreflangComponent } from './preflang/preflang.component';*/




const pagesRoutes: Routes = [
    {   
        path: '',
        component: PagesComponent,
        children: [
            { path: 'dashboard', component: DashboardComponent },
            /*{ path: '', redirectTo:'/dashboard' ,pathMatch:'full'},*/
            { path: 'user-reg', component: UserRegComponent },
            { path: 'manage-user', component: ManageUserComponent },
            ]
    },


    {   
        path: 'my',
        component: MainComponent,
        children: [
            { path: 'dashboard', component: DashboardComponent },
            /*{ path: '', redirectTo:'/dashboard' ,pathMatch:'full'},*/
            { path: 'urus_pengguna', component: UruspenggunaComponent },
            { path: 'manage-user', component: ManageUserComponent },
            ]
    }

];

export const PAGES_ROUTES = RouterModule.forRoot(pagesRoutes, { useHash: true });